@import '~antd/dist/antd.css';

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.site-layout-content {
  background: #fff;
  padding: 0px;
  min-height: 280px;
}

.card{
  border : 1px solid #eee;
  border-radius:8px;
  padding:20px;
  width:150px;
  box-shadow:0px 2px 2px #ccc;
  display: inline-block;
}

.title{
  font-weight: bold;
  font-size: 18px;
  margin-bottom: 8px;
}

p{
  margin:0px;
  text-align: center;
}

.site-card-wrapper {
  background: #ececec;
  padding: 20px;
  display: inline-block;
  align-items:center;
}

.layout{
    padding: 0px;
}
